@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
img {
  height: auto;
  object-fit: contain;
  max-width: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
.page {
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
}
.page__main {
  flex-grow: 1;
}
.container {
  max-width: 580px;
}
@media (min-width: 768px) {
  .container {
    max-width: 1100px;
  }
}
@media (min-width: 1340px) {
  .container {
    max-width: 1220px;
  }
}
.button {
  display: inline-block;
  padding: 10px 15px;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  background-color: #5eaa2f;
  color: rgba(255, 255, 255, 0.3);
}
.button--more {
  color: #444444;
  background-color: #f2f2f2;
}
@media (max-width: 767px) {
  .page-header__nav {
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: -2px;
  }
}
@media (min-width: 768px) {
  .page-header__nav {
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 89px;
    max-width: 1160px;
    margin: 0 auto;
    padding: 26px 30px 24px;
  }
}
@media (min-width: 1340px) {
  .page-header__nav {
    gap: 428px;
    max-width: 1340px;
    padding: 53px 60px 39px;
  }
}
@media (max-width: 767px) {
  .page-header__nav--closed .site-list {
    display: none;
  }
  .page-header__nav--closed .page-header__toggle {
    background-image: url("../../img/icons/burger-icon.svg");
  }
}
@media (max-width: 767px) {
  .page-header__nav--opened .site-list {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 5;
    margin-top: -1px;
    display: block;
  }
  .page-header__nav--opened .page-header__toggle {
    background-image: url("../../img/icons/close-icon.svg");
  }
}
.page-header__nav--no-js .page-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .page-header__bar {
    display: grid;
    grid-template-columns: 34px 1fr 34px;
    margin: 0 auto;
    max-width: 620px;
    padding: 10px 20px 18px;
    gap: 20px;
  }
}
.page-header__logo {
  display: grid;
  align-items: center;
  grid-template-columns: 34px 1fr;
  gap: 16px;
}
@media (max-width: 767px) {
  .page-header__logo {
    grid-column: 1 / 3;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    grid-template-columns: 61px 1fr;
    gap: 8px;
  }
  .page-header__logo:hover {
    opacity: 0.8;
  }
  .page-header__logo:focus {
    opacity: 0.6;
  }
}
@media (min-width: 1340px) {
  .page-header__logo {
    grid-template-columns: 71px 1fr;
  }
}
.page-header__logo-img {
  display: block;
  width: 34px;
  height: 38px;
}
@media (min-width: 768px) {
  .page-header__logo-img {
    width: 61px;
    height: 50px;
  }
}
@media (min-width: 1340px) {
  .page-header__logo-img {
    width: 71px;
    height: 59px;
  }
}
.page-header__logo-text {
  margin-top: 5px;
  margin-left: 4px;
  justify-self: center;
  max-width: 100%;
}
@media (min-width: 1340px) {
  .page-header__logo-text {
    width: 118px;
    height: 21px;
  }
}
.page-header__toggle {
  margin-top: 3px;
  margin-left: 4px;
  align-self: center;
  width: 34px;
  height: 34px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: center center;
}
.page-header__toggle:hover {
  background-color: transparent;
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  list-style-type: none;
}
