  .site-list {
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
    column-gap: 23px;
    row-gap: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
  }
}
@media (min-width: 1340px) {
  .site-list {
    column-gap: 22px;
