.site-list__item {
  position: relative;
}
@media (max-width: 767px) {
  .site-list__item {
    padding: 11px 20px 14px;
    border-top: 1px solid #e6e6e6;
  }
}
@media (min-width: 768px) {
  .site-list__item--current::after {
    position: absolute;
    content: "";
    left: 5px;
    right: 5px;
    bottom: 0;
    height: 2px;
    background-color: #68b738;
  }
}
.site-list__item--current .site-list__link {
  cursor: default;
}
@media (max-width: 767px) {
  .site-list__item--end {
    border-bottom: 1px solid #e6e6e6;
  }
}
.site-list__link {
  display: inline-block;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  color: inherit;
  text-decoration: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 18px 5px 12px;
  }
  .site-list__link:hover[href] {
    color: rgba(0, 0, 0, 0.6);
  }
  .site-list__link:active[href] {
    color: rgba(0, 0, 0, 0.3);
  }
}
@media (min-width: 1340px) {
  .site-list__link {
    padding-top: 15px;
    padding-bottom: 12px;
  }
}
@media (min-width: 1340px) {
  .site-list--contrast {
    color: #ffffff;
  }
}
@media (min-width: 1340px) {
  .site-list--contrast .site-list__item--current::after {
    background-color: #ffffff;
  }
}
@media (min-width: 1340px) {
  .site-list--contrast .site-list__link:hover[href] {
    color: rgba(255, 255, 255, 0.6);
  }
  .site-list--contrast .site-list__link:active[href] {
    color: rgba(255, 255, 255, 0.3);
  }
}
